.group-list-container {
    position: relative;
    flex-grow: 1;
    // height: 90vh;
    max-height: 100%;

    .group-list-wrapper {
        // height: 100%;
        // padding: 10px;
        padding: 0px 10px;
        bottom: 0;
        left: 0;
        margin-bottom: 8px;
        overflow-x: auto;
        overflow-y: hidden;
        padding-bottom: 8px;
        position: absolute;
        right: 0;
        top: 0;
        -webkit-user-select: none;
        user-select: none;
        white-space: nowrap;

        &::-webkit-scrollbar {
            height: 12px;
        }

        &::-webkit-scrollbar-thumb {
            background-color: rgba(235, 235, 235, 0.425);
            border-radius: 5px;
        }

        &::-webkit-scrollbar-track {
            border-radius: 5px;
            margin: 0 5px;
            background-color: rgba(119, 119, 119, 0.378);
        }

        .group-list {
            height: 100%;
        }
    }

    .add-group-container {
        margin: 0 4px;
        height: fit-content;
        min-width: 272px;
        max-width: 272px;

        form {
            padding: 8px;
        }

        .add-boarditem-btn {
            color: white;
            gap: 5px;
            border-radius: 5px;
            background-color: #dfe0db3c;
            padding-inline-start: 10px;
            height: 40px;
            width: 100%;
            transition: background-color 0.2s;

            &:hover {
                background-color: #dfe0db60;
            }
        }
    }
}

@include for-mobile-layout {
    .group-list-container {

        .group-list-wrapper {
            padding: 0;
        }
    }
}