.board-add {
    z-index: 30;
    height: 100px;
    width: 404px;
    display: flex;
    gap: 4px;
    position: absolute;
    top: 45px;
    left: 50%;
    transform: translateX(-50%);
    .chosen {
         ::after {
            content: "v";
        }
    }
    .board-add-form {
        width: 300px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-size: cover;
        background-position: 50%;
        .board-title-input {
            border: none;
            margin: 5px;
            background: transparent;
            color: #fff;
            line-height: rem(24px);
            font-weight: 700;
            font-size: rem(18px);
            padding: 2px, 8px;
            &:hover {
                background-color: rgba(216, 216, 216, 0.411);
            }
            &:focus {
                background-color: rgba(216, 216, 216, 0.411);
                outline: none;
            }
            &.editing {
                background-color: rgba(216, 216, 216, 0.411);
            }
        }
        .create-btn {
            background-color: #f4f5f7;
            color: white;
            align-self: flex-start;
            position: relative;
            top: 40px;
            padding: 6px 12px;
            line-height: 20px;
            border-radius: 3px;
            cursor: not-allowed;
            &.clickable {
                background-color: #0079bf;
                pointer-events: unset;
                cursor: pointer;
            }
        }
    }
    .images-container {
        width: 100px;
        display: flex;
        flex-wrap: wrap;
        gap: 5px;
        .img-btn {
            height: 28px;
            width: 28px;
            padding: 0;
            .add-board-img {
                height: 100%;
                object-fit: cover;
                border-radius: 5px;
            }
        }
    }
}

@include for-mobile-layout {
    .board-add {
        width: 100%;
        justify-content: space-between;
        .board-add-form {
            width: 250px;
            margin-left: 5px;
        }
    }
}